export const SaldosColumn = [
    {
        field: 'fullName',
        headerName: 'Nombre',
        // description: 'This column has a value getter and is not sortable.',
        // sortable: false,
        width: 160,
        valueGetter: (params) =>
            `${params.row.cliente || ''}`,
    },
    {
        field: 'fecha_prestamo',
        headerName: 'Fecha Prestamo',
        width: 120,
        editable: false,
        valueFormatter: (params) => params.value.split('T')[0]
    },
    {
        field: 'valor_prestamo',
        headerName: 'Valor Prestamo',
        width: 150,
        editable: false,
        valueFormatter: (params) => new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(params.value)
    },
    {
        field: 'tasa_interes',
        headerName: 'Tasa',
        width: 80,
        editable: false,
        valueFormatter: (params) => params.value + '%'
    },
    {
        field: 'capital_actual',
        headerName: 'Capital',
        width: 150,
        editable: false,
        valueFormatter: (params) => new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(params.value)
    },
    {
        field: 'valor_interes',
        headerName: 'Interes',
        width: 150,
        editable: false,
        valueFormatter: (params) => new Intl.NumberFormat('en-US', { style: 'currency', currency: 'USD' }).format(params.value)
    },
    {
        field: 'ultimo_pago',
        headerName: 'Fecha_corte',
        width: 120,
        editable: false,
        valueGetter: (params) => {
            if (params?.row.pagos?.length > 0) {
                let maxDateObject = params.row.pagos.reduce((prev, curr) => {
                    let prevDate = new Date(prev.date);
                    let currDate = new Date(curr.date);
                    return (prevDate > currDate) ? prev : curr;
                });
                return (maxDateObject?.fecha_pago).split('T')[0];
            }
            return params?.row.fecha_prestamo.split('T')[0]
        }

    }


]

